___CSS_LOADER_EXPORT___.push([module.id, `/* Created by: Surya Prakash N - 8924019 */

/* Global styling for buttons */
.btn {
  padding: 12px 20px;
  margin: 10px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.3s ease;
}

/* Button for teal color */
.btn-teal {
  background-color: #008080; /* Teal color */
  color: white;
  border: none;
}

.btn-white {
  background-color: white;
  color: #008080; /* Matching teal color */
  border: 2px solid #008080;
}

/* Hover effects for buttons */
.btn:hover {
  opacity: 0.8;
}

/* Header styling */
.header {
  text-align: center;
  margin-bottom: 40px;
}

.header h1 {
  font-size: 2.5rem;
  color: #333;
}

.header p {
  color: #666;
  font-size: 1rem;
}

/* Centered toggle button styling */
.toggle {
  display: flex;
  justify-content: center; /* Center the buttons horizontally */
  gap: 20px; /* Add spacing between the buttons */
  margin-bottom: 30px;
}

.toggle button {
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
}

.toggle button.active {
  background-color: #008080; /* Active teal button */
  color: white;
}

.toggle button:hover {
  opacity: 0.8;
}

/* Pricing section layout */
.pricing-section {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin: 0 20px;
}

.card {
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  text-align: center;
  background-color: #f9f9f9;
}

.card h2 {
  color: #333;
}

.card .price {
  font-size: 1.5rem;
  font-weight: bold;
  margin: 10px 0;
}

.card ul {
  list-style-type: none;
  padding: 0;
  margin-bottom: 20px;
}

.card ul li {
  margin: 5px 0;
}

.card button {
  background-color: #008080; /* Matching teal color */
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.3s ease;
}

/* Button hover effect */
.card button:hover {
  background-color: #006666; /* Darker teal shade */
}

/* Responsive design */
@media (max-width: 768px) {
  .pricing-section {
    grid-template-columns: 1fr;
  }

  .btn {
    width: 100%;
    margin: 10px 0;
  }
}
